.login {
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex-grow: 1;
  padding: 0 10px;
  gap: 10px;
}

.login__form {
  padding: 20px;
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  transition: 0.5s;
}

.login__form__label {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.login__form__label__text {
  font-size: 12px;
}

.login__form__label__input {
  padding: 10px;
  border: none;
  border-radius: 0.5em;
  outline: none;
  min-width: 250px;
  transition: 0.5s;
}

.login__form__error {
  font-size: 10px;
  color: #d63030;
}

.login__form__button {
  padding: 10px 20px;
  background-color: #2097e6;
  border: none;
  color: #fff;
  border-radius: 0.5em;
  cursor: pointer;
  transition: 0.5s;
}

.login__form__button:disabled {
  cursor: not-allowed;
  background-color: #c1c1c1;
  opacity: 0.2;
}

.login__form__loading {
  height: 36px;
  color: #999;
}

.login__saved {
  display: flex;
  gap: 10px;
}

.login__saved__item {
  /* padding: 10px; */
  border: none;
  display: flex;
  align-items: center;
}

.login__saved__item__button {
  align-items: center;
  flex-grow: 1;
  display: flex;
  gap: 5px;
  padding: 10px;
  background: none;
  border: none;
  cursor: pointer;
}

.login__saved__item__button__icon {
  font-size: 35px;
  color: var(--accent);
}

.login__saved__item__date {
  /* color: var(--accent); */
}

.login__saved__item__delete {
  margin-inline-start: 15px;
  padding: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  color: #fd2d2d;
  cursor: pointer;
  border: none;
}
