* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: google;
}

@font-face {
  font-family: google;
  src: url("./fonts/ProductSans/ProductSans-Regular.ttf");
}

@keyframes toTop {
  0% {
    opacity: 0%;
    transform: translateY(10px);
  }
  100% {
    opacity: 100%;
    transform: translateY(0);
  }
}

@keyframes toRight {
  0% {
    /* opacity: 0%; */
    transform: translateX(-10px);
  }
  100% {
    /* opacity: 100%; */
    transform: translateX(0);
  }
}

:root {
  --light: #fff;
  --light-2: #eee;
  --dark: #000;
  --dark-2: #222;
  --accent: #495aff;
}

.bg-light {
  background-color: var(--light-2);
}

.bg-light-2 {
  background-color: var(--light);
}

.bg-light-invert {
  background-color: #333;
}

.bg-dark {
  background-color: var(--dark);
}

.bg-dark-2 {
  background-color: var(--dark-2);
}

.bg-dark-invert {
  background-color: var(--light);
}

.c-dark {
  color: #c1c1c1;
}

.c-light {
  color: #333;
}

.c-dark-1 {
  color: #707070;
}

.c-light-1 {
  color: #999;
}

.c-dark-2 {
  color: var(--dark-2);
}

.c-light-2 {
  color: var(--light);
}

/*********** Baseline, reset styles ***********/

input[type="range"] {
  -webkit-appearance: none;
  appearance: none;
  height: 3px;
  cursor: pointer;
  /* width: 25rem; */
  background-color: var(--accent);
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none; /* Override default look */
  appearance: none;
  margin-top: -1px; /* Centers thumb on the track */
  background-color: var(--accent);
  border-radius: 50px;
  height: 1.2rem;
  width: 1.2rem;
}
